.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  font-family: "Lato", Arial, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  padding: 0;
  margin: 0;
}
.input-text {
  display: flex;
  width: 100%;
  column-gap: 13px;
}
.input-text__label {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  display: block;
  padding-top: 5px;
}
.input-text__input {
  border: 1px solid #e7e7e7;
  flex-grow: 1;
  height: 45px;
  padding-left: 7px;
}
.radio__label {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  display: inline-block;
  padding-left: 45px;
  position: relative;
  min-height: 30px;
  box-sizing: border-box;
  padding-top: 3px;
}
.radio__label::before {
  content: "";
  width: 30px;
  height: 30px;
  position: absolute;
  left: 0;
  top: 0;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
}
.radio__label::after {
  content: "";
  width: 13px;
  height: 13px;
  position: absolute;
  left: 10px;
  top: 10px;
  background-color: #68b738;
  border-radius: 50%;
}
.params {
  padding: 0;
  margin: 0;
  list-style: none;
}
.params__item {
  margin-bottom: 10px;
}
.program-form__block {
  padding: 0;
  margin: 0;
  border: none;
}
.program-type {
  padding: 0;
  margin: 0;
  list-style: none;
}
.program-type__item {
  margin-bottom: 10px;
}
